main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 auto 75px;
}
.search-box  .search-ban {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 25px;
  transition: 0.4 ease;
}
.search-box  .search-ban:focus{
  background-color: rgba(255,255,255,0.75);
}
.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50 70, 0.5);
}
.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.weather-box .temp{
  position: relative;
  display: block;
  max-width: 25%;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);

border-radius: 16px;
color: #fff;
font-size: 102px;
font-weight: 900;
text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
text-align: center;
box-shadow: 3px 6px rgba(50, 50, 70, 0.2);

}
.weather-box .weather{
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  text-align: center;
}

@media only screen and (max-width: 1100px){
   .weather-box .temp{
       font-size: 55px;
       max-width: 35%;
   }
}
